/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import ServerSideRender from '@wordpress/server-side-render';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		return <Fragment>
			<div className={ `wptravel-block-wrapper wptravel-block-trip-enquiry wptravel-block-preview` }>
				<ServerSideRender
					block="wptravel/trip-enquiry"
				/>
			</div>
		</Fragment>;
	}
}
export default edit;
