import {Component} from '@wordpress/element'
import { generateCustomStyleCode } from '../../extensions/custom-styles';
class BaseWrapper extends Component{
	componentDidMount() {
		const { attributes, setAttributes, clientId } = this.props;
		const { blockStyles, isSaved } = attributes;
		let saveIt = false;
		if ( ! isSaved ) {
			setAttributes( { isSaved: true, blockClientId: clientId } );
			saveIt = true;
		}
		generateCustomStyleCode( blockStyles, { ...this.props });
	}

	render(){
		const {children, clientId} = this.props;
		return <div id={`wptravel-section-${clientId}`}>{children}</div>;
	}
}
export default BaseWrapper;
