/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { BlockControls, AlignmentControl, InspectorControls } from '@wordpress/block-editor';
import { ToolbarGroup, ToggleControl, PanelBody } from '@wordpress/components';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { isEditing, setAttributes, attributes, openBlockSetting } = this.props;
		const { textAlign, showFromLabel, hideRegularPrice } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
					<AlignmentControl
						value={ textAlign }
						onChange={ ( nextAlign ) => {
							setAttributes( { textAlign: nextAlign } );
						} }
					/>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Trip Price Options' ) } >
						<ToggleControl
							label={ __( 'Show From' ) }
							checked={ showFromLabel }
							onChange={ () => setAttributes( { showFromLabel: ! showFromLabel } ) }
						/>
						<ToggleControl
							label={ __( 'Hide Regular Price' ) }
							help={ __( 'This will hide regular price in case of enabled sale price.' ) }
							checked={ hideRegularPrice }
							onChange={ () => setAttributes( { hideRegularPrice: ! hideRegularPrice } ) }
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	}
}

const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);
