/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl, PanelColorSettings } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, TextControl, SelectControl, ToggleControl } from '@wordpress/components';
import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setIsEditing, isEditing, setAttributes, attributes } = this.props;
		const { textAlign, reviewColor } = attributes;
		const colorSettings = [
			{
				value: reviewColor,
				onChange: colorValue => setAttributes({ reviewColor: colorValue }),
				label: __( "Review Color", 'wp-travel-blocks' )
			}
			
		];
		return (
			<Fragment>
				<BlockControls>
					<AlignmentControl
						value={textAlign}
						onChange={ ( nextAlign ) => {
							setAttributes( { textAlign: nextAlign } );
						} }
					/>
					<InspectorControls>
						<PanelColorSettings
							title={__("Colors")}
							colorSettings={colorSettings}
							initialOpen={false}
						/>
					</InspectorControls>
				</BlockControls>
			</Fragment>
		);
	}
}

export default Inspector;
