/**
 * WordPress dependencies
 */
import { Component, Fragment } from "@wordpress/element";
import {
	BlockControls,
	InspectorControls,
	JustifyContentControl,
	PanelColorSettings
} from "@wordpress/block-editor";
import { ToolbarGroup } from "@wordpress/components";
import { edit } from "@wordpress/icons";
import { __ } from "@wordpress/i18n";
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { openBlockSetting, isEditing, setAttributes, attributes } = this.props;
		const { contentJustification, textColor, textHoverColor } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __("Edit"),
				onClick: () => openBlockSetting(),
				isPressed: isEditing
			}
		];

		const colorSettings = [
			{
				value: textColor,
				onChange: colorValue => setAttributes({ textColor: colorValue }),
				label: __("Text Color")
			},
			{
				value: textHoverColor,
				onChange: colorValue => setAttributes({ textHoverColor: colorValue }),
				label: __("Hover Color")
			}
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={toolbarControls} />
					<JustifyContentControl
						allowedControls={["left", "center", "right"]}
						value={contentJustification}
						onChange={value => setAttributes({ contentJustification: value })}
						popoverProps={{
							position: "bottom right",
							isAlternate: true
						}}
					/>
				</BlockControls>
				<InspectorControls>
					<PanelColorSettings
						title={__("Colors")}
						colorSettings={colorSettings}
						initialOpen={false}
					/>
				</InspectorControls>
			</Fragment>
		);
	}
}
const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);
