/**
 * WordPress dependencies
 */
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl } from '@wordpress/block-editor';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { ExternalLink, PanelRow, PanelBody, ToolbarGroup } from '@wordpress/components';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";
import { __ } from '@wordpress/i18n';
/**
 * Inner dependencies.
 */

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { isEditing, allData, setAttributes, attributes, openBlockSetting } = this.props;
		const { textAlign } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />

					<AlignmentControl
						value={ textAlign }
						onChange={ ( nextAlign ) => {
							setAttributes( { textAlign: nextAlign } );
						} }
					/>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Trip Code Options', 'wp-travel' ) } >
						{ applyFilters( 'wp_travel_trip_edit_block_trip_code_inspector_controls', [], allData ) }
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	}
}
const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);

const __i18n = {
	...window._wp_travel_admin.strings,
};

addFilter( 'wp_travel_trip_edit_block_trip_code_inspector_controls', 'WPTravel/TripEdit/Block/TripCode/Notice', ( content ) => {
	return [ ...content, <PanelBody key="WPTravel-TripEdit-Block-TripCode-Notice">
		{ __i18n.notices.trip_code_option.description }<br />

		<ExternalLink href="https://wptravel.io/wp-travel-pro" className="wp-travel-upsell-badge">{ __i18n.notice_button_text.get_pro }</ExternalLink>
	</PanelBody> ];
} );
