import { addQueryArgs } from '@wordpress/url';
/**
 * Internal dependencies
 */
import * as actions from './actions';

export function* getSettings(queryArgs) {
	let args = { 
		action: 'wp_travel_filter_trips', _nonce: window._wp_travel._nonce
	}
	if (queryArgs.per_page) {
		args.numberposts = queryArgs.per_page;
	}
	const query = addQueryArgs( window.ajaxurl, args );
	const settings = yield actions.apiFetch( { url: query } );

	return actions.setSettings( settings );
}
