/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
const { __ } = wp.i18n; // Import __() from wp.i18n
import { Disabled, ToggleControl, QueryControls, Spinner } from '@wordpress/components'; // Import Setting Controls
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
import { store as coreStore } from '@wordpress/core-data';
import ServerSideRender from '@wordpress/server-side-render';

/**
 * External dependencies.
 */
import { isUndefined, pickBy, get, isEmpty, has } from 'lodash';

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

import { wpTravelFormat } from './functions';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );
		// const { getTripData } = select( 'WPTravel/TripEdit' );
		// const { getEntityRecords } = select( coreStore );
		// this.getTripData = getTripData.bind( this );
		// this.getEntityRecords = getEntityRecords.bind( this );

		this.state = {
			isEditing: false,
			tripTypes: [],
			locations: [],
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}

	render() {
		const { attributes, isSelected } = this.props;

		return ( <Fragment>
			{ isSelected && (
				<Inspector
					{ ...this.props }
					setIsEditing={ this.setIsEditing }
					isEditing={ this.state.isEditing }
				/>
			) }
			<ServerSideRender
				block="wptravel/trips-list"
				attributes={ attributes }
			/>
		</Fragment>
		);
	}
}

export default compose( [
	withSelect( ( select, props ) => {
		const { query } = props.attributes;
		const { getEntityRecords, getMedia, getUser } = select( coreStore );
		const { getSettings } = select( 'WPTravel/trips' );

		const { getCurrentPost } = select( 'core/editor' );

		const useUpdatedQueryControls = QueryControls.toString().includes( 'selectedCategories' );
		const currentPost = getCurrentPost();

		const newQuery = {
			...query,
		};

		// GET

		const updatedQuery = () => {
			const tripTypeIds = newQuery.selectedTripTypes && newQuery.selectedTripTypes.length > 0
				? newQuery.selectedTripTypes.map( ( cat ) => cat.id )
				: [];
			const tripDestinationIds = newQuery.selectedTripDestinations && newQuery.selectedTripDestinations.length > 0
				? newQuery.selectedTripDestinations.map( ( cat ) => cat.id )
				: [];
			const tripActivityIds = newQuery.selectedTripActivities && newQuery.selectedTripActivities.length > 0
				? newQuery.selectedTripActivities.map( ( cat ) => cat.id )
				: [];
			const tripKeywordIds = newQuery.selectedTripKeywords && newQuery.selectedTripKeywords.length > 0
				? newQuery.selectedTripKeywords.map( ( cat ) => cat.id )
				: [];

			const latestPostsQuery = pickBy( {
				order: newQuery.order,
				orderby: newQuery.orderBy,
				per_page: newQuery.numberOfItems,
				exclude: currentPost.id,
			}, ( value ) => ! isUndefined( value ) );

			if ( tripTypeIds.length > 0 ) {
				latestPostsQuery.itinerary_types = tripTypeIds;
			}
			if ( tripDestinationIds.length > 0 ) {
				latestPostsQuery.travel_locations = tripDestinationIds;
			}
			if ( tripActivityIds.length > 0 ) {
				latestPostsQuery.activity = tripActivityIds;
			}
			if ( tripKeywordIds.length > 0 ) {
				latestPostsQuery.travel_keywords = tripKeywordIds;
			}

			const latestTrips = getSettings( latestPostsQuery ); // Note: This will render trips only if the trip is fixed departure date and have trip date added.
			return ! isEmpty( latestTrips ) && latestTrips.map( ( post ) => {
				if ( post.featured_media ) {
					const image = getMedia( post.featured_media );
					let url = get(
						image,
						[
							'media_details',
							'sizes',
							'source_url',
						],
						null
					);
					if ( ! url ) {
						url = get( image, 'source_url', null );
					}
					post = {
						...post,
						featured_media_object: post.featured_media && select( 'core' ).getMedia( post.featured_media ),
					};
				}

				if ( ! isEmpty( post.categories ) ) {
					const catQuery = { per_page: -1, context: 'view', include: post.categories };
					const terms = getEntityRecords( 'taxonomy', 'category', catQuery );
					post = {
						...post,
						categories: terms,
					};
				}
				if ( ! isEmpty( post.itinerary_types ) ) {
					const catQuery = { per_page: -1, context: 'view', include: post.itinerary_types };
					const terms = getEntityRecords( 'taxonomy', 'itinerary_types', catQuery );
					post = {
						...post,
						itinerary_types: terms,
					};
				}
				if ( post.author ) {
					const author = getUser( post.author, {
						context: 'view',
					} );
					post = {
						...post,
						authorInfo: author,
					};
				}
				post = {
					...post,
				};

				return post;
			} );
		};

		return {
			latestTrips: useUpdatedQueryControls ? updatedQuery() : [],
			useUpdatedQueryControls,
		};
	} ),
] )( edit );

