/**
 * WordPress dependencies.
 */
import { Component } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
import { _n } from '@wordpress/i18n'; // Import __() from wp.i18n
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, isSelected, allData } = this.props;
		const { reviews } = allData;
		const { textAlign } = attributes;
		const textAlignClass = `has-text-align-${ textAlign }`;
		return <>
			{ isSelected && <Inspector { ...this.props } /> }
			{ 'undefined' !== typeof reviews &&
				<div className={ `wptravel-block-wrapper wptravel-block-preview wptravel-block-trip-review ${ textAlignClass }` }>
					<a href="/#" className="wp-travel-count-info">
						{ _n( `${ reviews } Review`, `${ reviews } Reviews`, parseInt( reviews ) ) }
					</a>
				</div>
			}
		</>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
		tripCode: tripData.trip_code,
	};
} ) )( edit );

