import { Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';
import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { RichText } from '@wordpress/block-editor';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

import { has } from 'lodash';

const Outline = compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		outline: has( tripData, 'trip_outline' ) ? tripData.trip_outline : '',
	};
} ),
withDispatch( ( dispatch ) => {
	const { updateTripData } = dispatch( 'WPTravel/TripEdit' );
	return {
		updateOutline( outline ) {
			updateTripData( {
				trip_outline: outline,
			} );
			const { editPost } = dispatch( 'core/editor' );
			editPost( {
				meta: {
					wp_travel_outline: outline,
				},
			} );
		},
	};
} ) )( ( { outline, updateOutline } ) => {
	return <RichText
		tagName="p" // The tag here is the element output and editable in the admin
		value={ outline } // Any existing content, either from the database or an attribute default
		// allowedFormats={ [] } // Allow the content to be made bold or italic, but do not allow other formatting options
		onChange={ ( content ) => {
			updateOutline( content );
		} } // Store updated content as a block attribute
		placeholder={ __( 'Trip outline' ) } // Display this text before any content has been added by the user
		className="wp-travel-block-rich-text"
	/>;
} );

addFilter( 'wptravel_trip_edit_block_tab_trip_outline', 'WPTravel/Block/TripEdit/Tab/TripOutline/Outline', ( content ) => {
	return [ ...content, <Fragment key="wp_travel_block_trip_edit_tab_trip_outline_outline">
		{/* <h3 className="wp-travel-tab-content-title">{ __( 'Trip Outline' ) }</h3> */}
		<Outline />
	</Fragment> ];
}, 9 );
