import { Component, Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	render() {
		const { attributes, isSelected, tripCode } = this.props;
		const { textAlign } = attributes;
		const textAlignClass = `has-text-align-${ textAlign }`;
		return <>
			{ isSelected && <Inspector { ...this.props } /> }
			<div className={ `wptravel-block-wrapper wptravel-block-trip-code wptravel-block-preview ${ textAlignClass }` }><code>{ tripCode }</code>
			</div>
		</>;
	}
}

export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
		tripCode: tripData.trip_code,
	};
} ) )( edit );
