/**
 * External dependencis.
 */
import Autocomplete from 'react-google-autocomplete';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { PanelBody, ToolbarGroup } from '@wordpress/components';
import { edit } from '@wordpress/icons';
import { applyFilters } from '@wordpress/hooks';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";
/**
 * Inner dependencies.
 */
import { generateCustomStyleCode } from '../../extensions/custom-styles';

/**
 * Inspector controls
 */
class Inspector extends Component {
	constructor( props ) {
		super( props );
		this.updateStyles = this.updateStyles.bind( this );
		this.state = {
			locationData: {},
		};
	}

	componentDidMount() {
		const { location } = this.props;
		this.setState( {
			locationData: location,
		} );
	}

	updateStyles( styles ) {
		const { setAttributes, clientId } = this.props;

		setAttributes( { blockClientId: clientId } );
		generateCustomStyleCode( styles, { ...this.props }, true );
		setAttributes( { blockAdvanceOptions: styles } );
	}

	render() {
		const { setIsEditing, isEditing, settings, location, openBlockSetting } = this.props;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit tabs' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
				</BlockControls>
				<InspectorControls>
					{applyFilters('wptravel_trip_edit_block_map_area', [], settings, location, this.props)}
				</InspectorControls>
			</Fragment>
		);
	}
}
const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);
