/**
 * WordPress dependencies.
 */
import { InnerBlocks } from '@wordpress/block-editor';

const edit = ( { attributes } ) => {
	const numberOfItems = attributes.postNumbers;
	const { query } = attributes;

	let tempAttr = attributes;
	let tempQuery = query;

	tempQuery.numberOfItems = numberOfItems;
	tempAttr.query = tempQuery;

	return <><InnerBlocks s allowedBlocks={ [ 'wptravel/trips-list' ] } template={ [ [
		'wptravel/trips-list',
		{ ...tempAttr },
	] ] } templateLock="all" /></>;
};

export default edit;