/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * External dependencies.
 */
import { isEmpty, has } from 'lodash';

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { isSelected, allData, attributes, settings } = this.props;
		const { contentJustification } = attributes;
		const blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';
		return (
			<div className={ `wptravel-block-wrapper wptravel-block-trip-fact ${ blockClass } is-content-justification-${ contentJustification }` }>
				{ isSelected && (
					<Inspector
						{ ...this.props }
						setIsEditing={ this.setIsEditing }
						isEditing={ this.state.isEditing }
					/>
				) }
				{/* { this.state.isEditing && (
					<>
						<strong>{ __( 'Trip Facts' ) }</strong>
						{ applyFilters( 'wp_travel_trip_edit_block_facts', [], allData, settings ) }
					</>
				) }
				{ ! this.state.isEditing && applyFilters( 'wp_travel_trip_edit_block_facts_view', [], allData, settings ) } */}
				{ applyFilters( 'wp_travel_trip_edit_block_facts_view', [], allData, settings ) }
			</div>
		);
	}
}

export default compose(
	withSelect( ( select ) => {
		const tripId = getCurrentPostId();
		const { getTripData } = select( 'WPTravel/TripEdit' );
		const settings = select( 'WPTravel/TripEdit' ).getSettings();
		const tripData = getTripData( tripId );

		return {
			allData: tripData,
			settings,
		};
	} )
)( edit );

addFilter(
	'wp_travel_trip_edit_block_facts_view',
	'WPTravel/TripEdit/Block/Facts/FactsView',
	( content, allData, settings ) => {
		const { wp_travel_trip_facts_settings: factSettings } = settings; // All Facts options from settings.
		const fieldTypeOption = ( factId ) => {
			if ( 'number' !== typeof factId ) {
				return [];
			}
			if ( 'object' !== typeof factSettings ) {
				return [];
			}

			const selectedFactSettings = Object.keys( factSettings ).length > 0 && 'undefined' !== typeof factSettings[ factId ] ? factSettings[ factId ].options : [];
			return Object.keys( selectedFactSettings ).map( ( index ) => {
				return {
					label: selectedFactSettings[ index ],
					value: 'option'+ ( ++index ), //Added for making compatible with selection option value.
				};
			} );
		};

		const facts =
			has( allData, 'trip_facts' ) && ! isEmpty( allData.trip_facts )
				? allData.trip_facts
				: [];
		return [
			...content,
			<Fragment key="wp_travel_trip_edit_block_facts_view">
				{ ( ! facts || ! facts.length ) && (
					<p className="description">
						{ __( 'No trip facts found. Please add Trip facts.' ) }
					</p>
				) }
				{ facts && (
					<div className="tour-info">
						<div className="tour-info-box clearfix">
							<div className="tour-info-column ">
								{ facts.map( ( fact, index ) => {
									const tripFactId = '' !== fact.fact_id ? parseInt( fact.fact_id ) : fact.fact_id; // To check if fact_id is not empty, if empty then it means newly added. So send it as string in order to prevent conflict in fiedltypeoption number typeof check. @since 4.4.1

									const singleOrMultipleOptions = fieldTypeOption( tripFactId );

									let singleSelected = singleOrMultipleOptions.filter( ( item ) => {
										return item.value === fact.value;
									} );
									singleSelected = typeof singleSelected !== 'undefined' ? singleSelected[ 0 ] : {};

									const multipleSelected = fact.value ? fact.value : [];

									if ( ! fact.value.length ) {
										return <></>;
									}
									return (
										<span
											className="tour-info-item tour-info-type"
											key={ `wp_travel_trip_edit_block_facts_view-${ index }` }
										>
											<span
												dangerouslySetInnerHTML={ { __html: fact.icon } }
											></span>
											<strong>{ fact.label }</strong>:
											{ fact.type === 'text' && <>{ fact.value }</> }
											{ fact.type === 'single' && <>{ singleSelected.label }</> }
											{ fact.type === 'multiple' &&
												<>
													{ singleOrMultipleOptions.map( ( factOption, i ) => {
														if ( multipleSelected.includes( factOption.value ) ) {
															return <span className="fact-value"> { factOption.label }
															</span>;
														}
													} ) }
												</>
											}
										</span>
									);
								} ) }
							</div>
						</div>
					</div>
				) }
			</Fragment>,
		];
	}
);
