/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, TextControl, SelectControl, ToggleControl } from '@wordpress/components';
import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setIsEditing, isEditing, attributes, setAttributes } = this.props;
		const { textAlign } = attributes;
		
		return (
			<Fragment>
				<BlockControls>
					<AlignmentControl
						value={textAlign}
						onChange={ ( nextAlign ) => {
							setAttributes( { textAlign: nextAlign } );
						} }
					/>
				</BlockControls>
			</Fragment>
		);
	}
}

export default Inspector;
