/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, TextControl, SelectControl, ToggleControl } from '@wordpress/components';
import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setIsEditing, isEditing, attributes, openBlockSetting, setAttributes } = this.props;
		const { showInput, showTripType, showLocation, showSubmit } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
					<InspectorControls>
						<PanelBody title={ __( 'Trip Search Options' ) } >
							<ToggleControl
								label={ __( 'Show Input' ) }
								checked={ showInput }
								onChange={ () => setAttributes( { showInput: ! showInput } ) }
							/>
							<ToggleControl
								label={ __( 'Show Trip Type' ) }
								checked={ showTripType }
								onChange={ () => setAttributes( { showTripType: ! showTripType } ) }
							/>
							<ToggleControl
								label={ __( 'Show Location' ) }
								checked={ showLocation }
								onChange={ () => setAttributes( { showLocation: ! showLocation } ) }
							/>
							{/* <ToggleControl
								label={ __( 'Show Submit' ) }
								checked={ showSubmit }
								onChange={ () => setAttributes( { showSubmit: ! showSubmit } ) }
							/> */}
						</PanelBody>
					</InspectorControls>
				</BlockControls>
			</Fragment>
		);
	}
}

const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);