/**
 * WordPress dependencies.
 */
import { InnerBlocks } from '@wordpress/block-editor';
import { store as coreStore } from '@wordpress/core-data';
import { withSelect, withDispatch } from '@wordpress/data';
import { compose } from '@wordpress/compose';


const edit = ( { attributes, tripTypes } ) => {

	const numberOfItems = attributes.postNumbers;
	const { query } = attributes;

	let tempAttr = attributes;
	let tempQuery = query;

	tempQuery.numberOfItems = numberOfItems;
	if ( tripTypes &&  attributes.tripType ) {
		const selectedTripTypes = tripTypes.length > 0 ? tripTypes.filter( ( cat ) => {return cat.id === parseInt( attributes.tripType ) } ) : [];
		tempQuery.selectedTripTypes = selectedTripTypes;
	}

	tempAttr.query = tempQuery;

	return <><InnerBlocks s allowedBlocks={ [ 'wptravel/trips-list' ] } template={ [ [
		'wptravel/trips-list',
		{ ...tempAttr },
	] ] } templateLock="all" /></>;
};

export default compose([
	withSelect((select) => {
		const { getEntityRecords } = select(coreStore);
		const termsQuery = { post_type: 'itineraries', per_page: 100 };

		// For Tax Query args.
		const _tripTypes = getEntityRecords('taxonomy', 'itinerary_types', termsQuery);
		return {
			tripTypes: _tripTypes,
		};
	} ),
	withDispatch( ( dispatch, ownProps ) => {
		return {
			openBlockSetting: () => {
				dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
				dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
			}
		};
	} )
] ) ( edit );
