/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, TextControl, SelectControl, ToggleControl } from '@wordpress/components';
import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";
/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setIsEditing, isEditing, openBlockSetting, attributes, setAttributes } = this.props;
		const {
			keyWordSearch,
			tripFact,
			tripTypeFilter,
			tripLocationFilter,
			priceOrderFilter,
			priceRangeFilter,
			tripDateFilter,
		} = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Trip Filter Options' ) } >
						<ToggleControl
							label={ __( 'Keyword Search' ) }
							checked={ keyWordSearch }
							onChange={ () => setAttributes( { keyWordSearch: ! keyWordSearch } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Fact' ) }
							checked={ tripFact }
							onChange={ () => setAttributes( { tripFact: ! tripFact } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Type Filter' ) }
							checked={ tripTypeFilter }
							onChange={ () => setAttributes( { tripTypeFilter: ! tripTypeFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Location Filter' ) }
							checked={ tripLocationFilter }
							onChange={ () => setAttributes( { tripLocationFilter: ! tripLocationFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Price Order Filter' ) }
							checked={ priceOrderFilter }
							onChange={ () => setAttributes( { priceOrderFilter: ! priceOrderFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Price Range Filter' ) }
							checked={ priceRangeFilter }
							onChange={ () => setAttributes( { priceRangeFilter: ! priceRangeFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Date Filter' ) }
							checked={ tripDateFilter }
							onChange={ () => setAttributes( { tripDateFilter: ! tripDateFilter } ) }
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	}
}
const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);

