/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, QueryControls, FormTokenField, SelectControl, ToggleControl } from '@wordpress/components';
import { withSelect, withDispatch } from '@wordpress/data';
import { edit } from '@wordpress/icons';
import { compose } from '@wordpress/compose';
import { store as coreStore } from '@wordpress/core-data';

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setAttributes, attributes, openBlockSetting } = this.props;
		const { query, saleTrip, featuredTrip } = attributes;
		const {
			orderBy,
			order,
			selectedCategories,
			selectedTripTypes,
			selectedTripDestinations,
			selectedTripActivities,
			selectedTripKeywords,
			numberOfItems,
		} = query;

		// const categorySuggestions = this.props.categories?.reduce(
		// 	( accumulator, category ) => ( {
		// 		...accumulator,
		// 		[ category.name ]: category,
		// 	} ),
		// 	{}
		// ) ?? {};

		// const suggestions = this.props.categories?.reduce(
		// 	( accumulator, category ) => ( {
		// 		...accumulator,
		// 		[ category.name ]: category,
		// 	} ),
		// 	{}
		// );

		const TripTypeSuggestions = this.props.tripTypes?.reduce(
			(accumulator, category) => ({
				...accumulator,
				[category.name]: category,
			}),
			{}
		) ?? {};
		let selected_trip_types = [];
		if (selectedTripTypes && selectedTripTypes.length > 0) {
			selected_trip_types = selectedTripTypes.map((type) => {
				return type.name;
			})
		}

		const TripDestinationSuggestions = this.props.tripDestinations?.reduce(
			(accumulator, category) => ({
				...accumulator,
				[category.name]: category,
			}),
			{}
		) ?? {};
		let selected_trip_destinations = [];
		if (selectedTripDestinations && selectedTripDestinations.length > 0) {
			selected_trip_destinations = selectedTripDestinations.map((type) => {
				return type.name;
			})
		}

		const TripActivitySuggestions = this.props.tripActivities?.reduce(
			(accumulator, category) => ({
				...accumulator,
				[category.name]: category,
			}),
			{}
		) ?? {};
		let selected_trip_activities = [];
		if (selectedTripActivities && selectedTripActivities.length > 0) {
			selected_trip_activities = selectedTripActivities.map((type) => {
				return type.name;
			})
		}

		const TripKeywordSuggestions = this.props.tripKeywords?.reduce(
			(accumulator, category) => ({
				...accumulator,
				[category.name]: category,
			}),
			{}
		) ?? {};
		let selected_trip_keywords = [];
		if (selectedTripKeywords && selectedTripKeywords.length > 0) {
			selected_trip_keywords = selectedTripKeywords.map((type) => {
				return type.name;
			})
		}
		const toolbarControls = [
			{
				icon: edit,
				title: __('Edit'),
				onClick: () => openBlockSetting(),
				// isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={toolbarControls} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Filters')}>
						<QueryControls
							{...{ orderBy, order, numberOfItems }}
							// onOrderByChange={(newOrderBy) => {
							// 	setAttributes({
							// 		query: {
							// 			...query,
							// 			orderBy: newOrderBy,
							// 		},
							// 	})
							// }}
							// onOrderChange={(newOrder) => {
							// 	setAttributes({
							// 		query: {
							// 			...query,
							// 			order: newOrder,
							// 		},
							// 	})
							// }}
							// categorySuggestions={ categorySuggestions }
							// selectedCategories={ selectedCategories }
							// onCategoryChange={ ( tokens ) => {
							// 	const allCategories = tokens.map( ( token ) =>
							// 		typeof token === 'string' ? suggestions[ token ] : token
							// 	);
							// 	setAttributes( {
							// 		query: {
							// 			...query,
							// 			selectedCategories: allCategories,
							// 		},
							// 	} );
							// } }
							onNumberOfItemsChange={(newNumberOfItems) =>
								setAttributes({
									query: {
										...query,
										numberOfItems: newNumberOfItems,
									},
								})
							}
						/>
						<SelectControl
							label={__('Order by')}
							value={ orderBy + '/' + order }
							options={ [
								{
									label: __('Newest to oldest'),
									value:'date/desc'
								}, {
									label: __('Oldest to newest'),
									value:'date/asc'
								}, {
									label: __('A → Z'),
									value:'title/asc'
								}, {
									label: __('Z → A'),
									value:'title/desc'
								}
							] }
							onChange={ ( selectedVal ) => {
								let values = selectedVal.split( '/' );
								let newOrderBy = values[0];
								let newOrder = values[1];
								setAttributes({
									query: {
										...query,
										orderBy: newOrderBy,
										order: newOrder,
									},
								});
							} }
						/>
						<FormTokenField
							label={__('Trip Type')}
							value={selected_trip_types ?? []}
							suggestions={Object.keys(TripTypeSuggestions).map(name => { return name })}
							onChange={ tokens => {
								const allCategories = tokens.map((token) => {
									const newData  = typeof token === 'string' ? TripTypeSuggestions[ token ] : token;
									const selected = {
										count: newData.count,
										id: newData.id,
										description: newData.description,
										link: newData.link,
										taxonomy: newData.taxonomy,
										name: newData.name,
										slug: newData.slug,
									}
									return selected;
								} );
								setAttributes({
									query: {
										...query,
										selectedTripTypes: allCategories,
									},
								} );
							} }
						/>
						<FormTokenField
							label={ __('Trip Destinations') }
							value={ selected_trip_destinations ?? [] }
							suggestions={Object.keys(TripDestinationSuggestions).map(name => { return name })}
							onChange={ tokens => {
								const allCategories = tokens.map((token) => {
									const newData  = typeof token === 'string' ? TripDestinationSuggestions[ token ] : token;
									const selected = {
										count: newData.count,
										id: newData.id,
										description: newData.description,
										link: newData.link,
										taxonomy: newData.taxonomy,
										name: newData.name,
										slug: newData.slug,
									}
									return selected;
								} );
								setAttributes({
									query: {
										...query,
										selectedTripDestinations: allCategories,
									},
								});
							}}
						/>

						<FormTokenField
							label={ __('Trip Activities') }
							value={ selected_trip_activities ?? [] }
							suggestions={Object.keys(TripActivitySuggestions).map(name => { return name })}
							onChange={tokens => {
								const allCategories = tokens.map((token) => {
									const newData  = typeof token === 'string' ? TripActivitySuggestions[ token ] : token;
									const selected = {
										count: newData.count,
										id: newData.id,
										description: newData.description,
										link: newData.link,
										taxonomy: newData.taxonomy,
										name: newData.name,
										slug: newData.slug,
									}
									return selected;
								} );
								setAttributes({
									query: {
										...query,
										selectedTripActivities: allCategories,
									},
								});
							}}
						/>
						<FormTokenField
							label={ __('Trip Keywords') }
							value={ selected_trip_keywords ?? [] }
							suggestions={ Object.keys(TripKeywordSuggestions).map(name => { return name }) }
							onChange={ tokens => {
								const allCategories = tokens.map((token) => {
									const newData  = typeof token === 'string' ? TripKeywordSuggestions[ token ] : token;
									const selected = {
										count: newData.count,
										id: newData.id,
										description: newData.description,
										link: newData.link,
										taxonomy: newData.taxonomy,
										name: newData.name,
										slug: newData.slug,
									}
									return selected;
								} );
								setAttributes({
									query: {
										...query,
										selectedTripKeywords: allCategories,
									},
								});
							}}
						/>
						<ToggleControl
							label={ __( 'Sale Trip' ) }
							checked={ saleTrip }
							onChange={ () => setAttributes( { saleTrip: ! saleTrip } ) }
						/>
						<ToggleControl
							label={ __( 'Featured Trip' ) }
							checked={ featuredTrip }
							onChange={ () => setAttributes( { featuredTrip: ! featuredTrip } ) }
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	}
}

export default compose([
	withSelect((select) => {
		const { getEntityRecords } = select(coreStore);
		const termsQuery = { post_type: 'itineraries', per_page: 100 };

		// For Tax Query args.
		const _tripTypes = getEntityRecords('taxonomy', 'itinerary_types', termsQuery);
		const _tripDestinations = getEntityRecords('taxonomy', 'travel_locations', termsQuery);
		const _tripActivities = getEntityRecords('taxonomy', 'activity', termsQuery);
		const _tripKeywords = getEntityRecords('taxonomy', 'travel_keywords', termsQuery);
		return {
			tripTypes: _tripTypes,
			tripDestinations: _tripDestinations,
			tripActivities: _tripActivities,
			tripKeywords: _tripKeywords,
		};
	} ),
	withDispatch( ( dispatch, ownProps ) => {
		return {
			openBlockSetting: () => {
				dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
				dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
			}
		};
	} )
] ) ( Inspector );

