/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

/**
 * External dependencies.
 */
import { has } from 'lodash';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, setAttributes, isSelected, allData } = this.props;
		const { textAlign } = attributes;
		const textAlignClass = `has-text-align-${ textAlign }`;
		return <>
			{ isSelected && <Inspector { ...this.props } /> }
			<div className={ `wptravel-block-wrapper wptravel-block-group-size wptravel-block-preview ${ textAlignClass }` }>
				{ applyFilters( 'wptravel_trip_edit_block_group_size_view', [], allData ) }
			</div></>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
	};
} ) )( edit );

addFilter( 'wptravel_trip_edit_block_group_size_view', 'WPTravel/TripEdit/Block/GroupSize/GroupSizeView', ( content, allData ) => {
	const groupSize = has( allData, 'group_size' ) && allData.group_size ? allData.group_size : 0;
	return [ ...content, <Fragment key="wptravel_trip_edit_block_group_size_view">
		{ groupSize > 0 ? <>{ `${ groupSize } Pax` }</> : <p className="description">{ __( 'No Size Limit.' ) }</p> }
	</Fragment> ];
} );
