/**
 * WordPress dependencies.
 */
import { InnerBlocks } from '@wordpress/block-editor';

const edit = ( { attributes } ) => {
	return <><InnerBlocks s allowedBlocks={ [ 'wptravel/trip-filters' ] } template={ [ [
		'wptravel/trip-filters',
		{ ...attributes },
	] ] } templateLock="all" /></>;
};

export default edit;
