/**
 * WordPress dependencies
 */
import { registerBlockType } from '@wordpress/blocks';

import { subscribe, select } from '@wordpress/data';
const { isSavingPost } = select( 'core/editor' );
import apiFetch from '@wordpress/api-fetch';
const __i18n = {
	..._wp_travel_admin.strings,
};

// Extensions
import './extensions/store';
import './extensions/plugin-setting-sidebar';

// Import deprecated blocks.
import * as deprecatedTravelFilter from './blocks/_deprecated-blocks/travel-filter';
import * as deprecatedTripSearchForm from './blocks/_deprecated-blocks/trip-search-form';
import * as deprecatedTripByType from './blocks/_deprecated-blocks/trip-type';
import * as deprecatedTripByLocation from './blocks/_deprecated-blocks/trip-location';
import * as deprecatedFeaturedTrip from './blocks/_deprecated-blocks/featured-trip';
import * as deprecatedSaleTrip from './blocks/_deprecated-blocks/sale-trip';

// Import blocks.
import * as breadcrumb from './blocks/breadcrumb';
import * as tripCalendar from './blocks/trip-calendar';
import * as tripCategories from './blocks/trip-categories';
import * as tripCode from './blocks/trip-code';
import * as tripDurationDate from './blocks/trip-duration';
import * as tripEnquiry from './blocks/trip-enquiry';
import * as tripFacts from './blocks/trip-facts';
import * as tripFilters from './blocks/trip-filters';
import * as tripGroupSize from './blocks/trip-group-size';
import * as tripMap from './blocks/trip-map';
import * as tripPrice from './blocks/trip-price';
import * as tripRating from './blocks/trip-rating';
import * as tripReview from './blocks/trip-review';
import * as tripSearch from './blocks/trip-search';
import * as tripTabs from './blocks/trip-tabs';
import * as tripWishlists from './blocks/trip-wishlists';
import * as tripsList from './blocks/trips-list';
import * as templates from './blocks/templates';
import * as overview from './blocks/trip-overview';
import * as includes from './blocks/trip-includes';
import * as excludes from './blocks/trip-excludes';
import * as outline from './blocks/trip-outline';
import * as faqs from './blocks/trip-faqs';
import * as gallery from './blocks/trip-gallery';
import * as reviewsList from './blocks/trip-reviews-list';

/**
 * Function to register an individual block.
 *
 * @param {Object} block The block to be registered.
 *
 */
const registerBlock = ( block ) => {
	if ( ! block ) {
		return;
	}

	const { category } = block;

	const { name, settings } = block;

	registerBlockType( name, {
		category,
		...settings,
	} );
};

/**
 * Function to register blocks.
 */
export const registerWPTravelBlocks = () => {
	[
		deprecatedTravelFilter,
		deprecatedTripSearchForm,
		deprecatedTripByType,
		deprecatedTripByLocation,
		deprecatedFeaturedTrip,
		deprecatedSaleTrip,
		tripMap,
		tripTabs,
		tripFacts,
		tripCode,
		tripFilters,
		tripSearch,
		tripEnquiry,
		tripPrice,
		tripRating,
		tripReview,
		tripGroupSize,
		tripDurationDate,
		breadcrumb,
		tripWishlists,
		tripCategories,
		tripsList,
		tripCalendar,
		templates,
		overview,
		includes,
		excludes,
		outline,
		faqs,
		gallery,
		reviewsList
	].forEach( registerBlock );
};

registerWPTravelBlocks();

// Trigger save block on update button click.
subscribe( () => {
	if ( isSavingPost() ) {
		let currentPostType = wp.data.select('core/editor').getCurrentPostType();
		if ( 'itineraries' !== currentPostType ) {
			return;
		}
		const allData = wp.data.select( 'WPTravel/TripEdit' ).getAllStore();
		const { updateStateChange } = wp.data.dispatch( 'WPTravel/TripEdit' );

		if ( allData.is_fixed_departure ) {
			let _pricingIds = [];
			allData.pricings.map( ( _price ) => {
				_pricingIds =
					false !== _price.id ? [ ..._pricingIds, _price.id ] : _pricingIds;
			} );
			if ( allData.dates.length > 0 ) {
				allData.dates.map( ( _dates, _datesIndex ) => {
					if ( ! allData.dates[ _datesIndex ].is_recurring ) {
						allData.dates[ _datesIndex ] = {
							...allData.dates[ _datesIndex ],
							...{
								years: '',
								months: '',
								weeks: '',
								days: '',
								date_days: '',
							},
						};
					}
				} );
			}
		} else {
			allData.dates = [];
		}

		if ( ! allData.enable_excluded_dates_times ) {
			allData.excluded_dates_times = [];
		}
		apiFetch( {
			url: `${ ajaxurl }?action=wp_travel_update_trip&trip_id=${ _wp_travel.postID }&_nonce=${ _wp_travel._nonce }`,
			data: allData,
			method: 'post',
		} ).then( ( res ) => {
			if ( res.success && 'WP_TRAVEL_UPDATED_TRIP' === res.data.code ) {
				updateStateChange( false );

				// @todo: site url must be localize.
				let url = window.location.href.split( '?' )[ 0 ];
				if ( url.includes( 'post-new.php' ) ) {
					const replaceString =
						'post.php?post=' + _wp_travel.postID + '&action=edit';
					url = url.replace( 'post-new.php', replaceString );
					history.pushState( null, '', url );
					jQuery( '#original_publish, #publish' ).val( __i18n.update );
				}
			}
		} );
	}
} );
