/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, JustifyContentControl, PanelColorSettings } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, PanelRow } from '@wordpress/components';

import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { allData, setIsEditing, isEditing, setAttributes, attributes, settings, openBlockSetting } = this.props;
		const { backgroundColor } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit tabs' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		const colorSettings = [
			{
				value: backgroundColor,
				onChange: colorValue => setAttributes({ backgroundColor: colorValue }),
				label: __( "Background Color", 'wp-travel-blocks' )
			}
		];

		const { contentJustification } = attributes;
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
					<JustifyContentControl
						allowedControls={ [
							'left',
							'center',
							'right',
						] }
						value={ contentJustification }
						onChange={ ( value ) =>
							setAttributes( { contentJustification: value } )
						}
						popoverProps={ {
							position: 'bottom right',
							isAlternate: true,
						} }
					/>
				</BlockControls>
				<InspectorControls>
					{/* { applyFilters( 'wp_travel_trip_edit_block_facts_inspector_controls', [], allData ) } */}
					<PanelBody title={ __( 'Fact options', 'wp-travel' ) } >
						{ applyFilters( 'wp_travel_trip_edit_block_facts', [], allData, settings ) }
					</PanelBody>
					<PanelColorSettings
						title={__("Colors")}
						colorSettings={colorSettings}
						initialOpen={false}
					/>
				</InspectorControls>
			</Fragment>
		);
	}
}

const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);
