/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { Disabled, ToggleControl } = wp.components; // Import Setting Controls
import ServerSideRender from '@wordpress/server-side-render';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';
/**
 * External dependencies.
 */
import { isEmpty, has } from 'lodash';
class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, setAttributes, isSelected, allData } = this.props;
		const { textAlign } = attributes;
		const textAlignClass = `has-text-align-${ textAlign }`;
		return <>
			{ isSelected && <Inspector { ...this.props } /> }
			<div className={ `wptravel-block-wrapper wptravel-block-preview wptravel-block-trip-duration ${ textAlignClass }` }>
				{ applyFilters( 'wptravel_trip_edit_block_trip_duration_date_view', [], allData ) }
			</div></>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
	};
} ) )( edit );

addFilter( 'wptravel_trip_edit_block_trip_duration_date_view', 'WPTravel/TripEdit/Block/TripDurationDate/TripDurationDateView', ( content, allData ) => {
	const isFixedDeparture = has( allData, 'is_fixed_departure' ) && allData.is_fixed_departure ? allData.is_fixed_departure : false;
	const { dates, display_dates: displayDates } = allData;
	const { days, nights } = has( allData, 'trip_duration' ) && allData.trip_duration ? allData.trip_duration : [];

	return [ ...content, <Fragment key="wptravel_trip_edit_block_trip_duration_date_view">
		{ isFixedDeparture ? <>
			{ dates ? <>{ displayDates }</> : <p className="description">{ __( 'Please add date first.' ) }</p> }

		</> : <>
			{ ( days || nights ) ? <>{ __( `${ days } Day(s) ${ nights } Nights(s) ` ) }</> : <p className="description">{ __( 'Please add trip duration first.' ) }</p> }
		</> }
	</Fragment> ];
} );
