/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
import { store as coreStore } from '@wordpress/core-data';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, isSelected, terms } = this.props;
		const { textAlign } = attributes;
		const textAlignClass = ! this.state.isEditing ? `has-text-align-${ textAlign }` : ''; // do not need text align class in edit mode
		const blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';
		return <div className={ `wptravel-block-wrapper wptravel-block-trip-categories ${ blockClass } ${ textAlignClass }` }>
			<Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } isSelected={isSelected} />

			{/* { ! this.state.isEditing && <> */}
			{ 'undefined' !== typeof terms && terms && terms.length > 0 ? <>
				{ terms.map( ( term, index ) => {
					return <Fragment key={ `term-${ index }` }>
						<a href={ term.link }>{ term.name } </a>
						{/* { index < terms.length - 1 && ', ' } */}
					</Fragment>;
				} ) }
			</> : <>{ __( 'N/A' ) } </> }
			{/* </> } */}

		</div>;
	}
}
export default compose( withSelect( ( select, ownProps ) => {
	const tripId = getCurrentPostId();
	const { getEntityRecords } = select( coreStore );
	const { attributes } = ownProps;
	const { tripTaxonomy } = attributes;
	const taxonomy = 'undefined' !== typeof tripTaxonomy && tripTaxonomy ? tripTaxonomy : 'itinerary_types';
	const catQuery = { per_page: -1, context: 'view', post: tripId };
	const terms = getEntityRecords( 'taxonomy', taxonomy, catQuery );
	return {
		terms,
	};
} ) )( edit );
