/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { allData, clientId, isSelected, attributes, setAttributes } = this.props;
		const { contentJustification, textColor } = attributes;
		let title = __( 'No Title' );
		if ( allData && allData.title ) {
			title = allData.title ? allData.title.rendered : allData.title.raw;
		}

		const textStyle = {};
		if ( textColor ) {
			textStyle.color = textColor;
		}
		return <Fragment>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			<div className={ `wptravel-block-wrapper wptravel-block-preview wptravel-block-breadcrumb is-content-justification-${ contentJustification }` } id={ `wptravel-block-${ clientId }` }>
				<div role="navigation" aria-label="Breadcrumbs" className="breadcrumb-trail breadcrumbs" itemProp="breadcrumb">
					<ul className="trail-items" itemScope="" itemType="http://schema.org/BreadcrumbList">
						<li style={ textStyle } itemProp="itemListElement" itemScope="" itemType="http://schema.org/ListItem" className="trail-item trail-begin">
							<a href="/#" rel="home"><span itemProp="name">{ __( 'Home' ) }</span></a>
						</li>
						<li style={ textStyle } itemProp="itemListElement" itemScope="" itemType="http://schema.org/ListItem" className="trail-item">
							<a href="/#"><span itemProp="name">{ __( 'Trips' ) }</span></a>
						</li>
						<li style={ textStyle } itemProp="itemListElement" itemScope="" itemType="http://schema.org/ListItem" className="trail-item trail-end">
							<span itemProp="name" style={ textStyle }>{ title }</span>
						</li>
					</ul>
				</div>
			</div>
		</Fragment>;
	}
}
export default compose( withSelect( ( select ) => {
	const { getCurrentPostId } = select( 'core/editor' );

	const tripId = getCurrentPostId();
	const res = select( 'core' ).getEntityRecords( 'postType', 'itineraries', { include: [ tripId ] } );
	return {
		allData: res.length > 0 ? res[ 0 ] : [],
	};
} ) )( edit );
