/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { Disabled, ToggleControl } = wp.components; // Import Setting Controls
import ServerSideRender from '@wordpress/server-side-render';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';


class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, setAttributes, isSelected, allData } = this.props;
		const {showFromLabel, hideRegularPrice, textAlign} = attributes;
		let textAlignClass = ! this.state.isEditing ? `has-text-align-${textAlign}` : ''; // do not need text align class in edit mode
		return <div className={`wptravel-block-wrapper wptravel-block-preview wptravel-block-wishlists ${textAlignClass}`}>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			{applyFilters( 'wptravel_trip_edit_block_wishlists_view', [], allData, attributes ) }
		</div>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
	};
} ) )( edit );


addFilter( 'wptravel_trip_edit_block_wishlists_view', 'WPTravel/TripEdit/Block/Wishlists/WishlistsView', ( content, allData, attributes ) => {
	const { trip_price, regular_price, enable_sale} = allData;
	const {showFromLabel, hideRegularPrice } = attributes;
	return [ ...content, <Fragment key="wptravel_trip_edit_block_wishlists_view">
		<a href="" className="wp-travel-add-to-wishlists"><i className="fas fa-bookmark"></i></a>
		
	</Fragment> ];
} );
