import { Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';
import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { RichText } from '@wordpress/block-editor';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

import { has } from 'lodash';

const Overview = compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		overview: has( tripData, 'trip_overview' ) ? tripData.trip_overview : '',
	};
} ),
withDispatch( ( dispatch ) => {
	const { updateTripData } = dispatch( 'WPTravel/TripEdit' );
	return {
		updateOverview( overview ) {
			updateTripData( {
				trip_overview: overview,
			} );
			const { editPost } = dispatch( 'core/editor' );
			editPost( {
				meta: {
					wp_travel_overview: overview,
				},
			} );
		},
	};
} ) )( ( { overview, updateOverview } ) => {
	return <RichText
		tagName="p" // The tag here is the element output and editable in the admin
		value={ overview } // Any existing content, either from the database or an attribute default
		// allowedFormats={ [] } // Allow the content to be made bold or italic, but do not allow other formatting options
		onChange={ ( content ) => {
			updateOverview( content );
		} } // Store updated content as a block attribute
		placeholder={ __( 'Overview' ) } // Display this text before any content has been added by the user
		className="wp-travel-block-rich-text"
	/>;
} );

addFilter( 'wptravel_trip_edit_block_tab_overview', 'WPTravel/TripEdit/Block/Tab/Overview/OverviewField', ( content ) => {
	return [ ...content, <Fragment key="wp_travel_block_trip_edit_tab_overview">
		{/* <h3 className="wp-travel-tab-content-title">{ __( 'Overview' ) }</h3> */}
		<Overview />
	</Fragment> ];
} );

addFilter( 'wptravel_trip_edit_block_tab_view_overview', 'WPTravel/TripEdit/Block/Tab/view/Overview/OverviewField', ( content, allData ) => {
	const overview = has( allData, 'trip_overview' ) ? allData.trip_overview : '';
	return [ ...content, <Fragment key="wp_travel_block_trip_edit_tab_overview">
		<RichText.Content tagName="p" value={ overview } />
	</Fragment> ];
} );
