/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { ToggleControl } = wp.components; // Import Setting Controls
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

import { wpTravelFormat } from './functions';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, setAttributes, isSelected, allData } = this.props;
		const { showFromLabel, hideRegularPrice, textAlign } = attributes;
		const textAlignClass = ! this.state.isEditing ? `has-text-align-${ textAlign }` : ''; // do not need text align class in edit mode
		// const blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';

		return <div className={ `wptravel-block-wrapper wptravel-block-trip-price wptravel-block-preview ${ textAlignClass }` }>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			{ applyFilters( 'wptravel_trip_edit_block_trip_price_view', [], allData, attributes ) }
		</div>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
	};
} ) )( edit );

addFilter( 'wptravel_trip_edit_block_trip_price_view', 'WPTravel/TripEdit/Block/TripPrice/TripPriceView', ( content, allData, attributes ) => {
	const { trip_price, regular_price, enable_sale } = allData;
	const { showFromLabel, hideRegularPrice } = attributes;
	return [ ...content, <Fragment key="wptravel_trip_edit_block_trip_price_view">
		{ trip_price ? <>
			<div className="trip-price" >
				{ showFromLabel && <span className="price-from">{ __( 'From' ) } </span> }
				{ ! hideRegularPrice && enable_sale && regular_price !== trip_price && <del><span dangerouslySetInnerHTML={ { __html: wpTravelFormat( regular_price ) } }></span></del> }
				<span className="person-count">
					<ins>
						<span dangerouslySetInnerHTML={ { __html: wpTravelFormat( trip_price ) } }></span>
					</ins>
				</span>
			</div>

		</> : <p className="description">
			{ __( 'Please add Price' ) }
		</p> }

	</Fragment> ];
} );

