/**
 * WordPress dependencies.
 */
import { Component } from '@wordpress/element';
import { applyFilters } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect } from '@wordpress/data';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	render() {
		const { attributes, setAttributes, isSelected, allData } = this.props;
		const { textAlign } = attributes;
		const textAlignClass = ! this.state.isEditing ? `has-text-align-${ textAlign }` : ''; // do not need text align class in edit mode

		return <div className={ `wptravel-block-wrapper wptravel-block-trip-overview wptravel-block-edit ${ textAlignClass }` }>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			{ applyFilters( 'wptravel_trip_edit_block_tab_overview', [], allData, attributes ) }
		</div>;
	}
}
export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		allData: tripData,
	};
} ) )( edit );
