import { Component, Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { RichText } from '@wordpress/block-editor';
import { Button } from '@wordpress/components';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

import { isEmpty, has, first } from 'lodash';

/**
 * Inner dependencies.
 */
import Inspector from './inspector';
import './tab-contents/outline';
import './tab-contents/excludes';
import './tab-contents/includes';
import './tab-contents/overview';
import './tab-contents/reviews';

const TabHeadings = ( props ) => {
	const { tabs, updateTabs, swithTab } = props;
	const tripTabs = ! isEmpty( tabs ) ? tabs : [];
	const updateLabel = ( label, newIndex ) => {
		tripTabs[ newIndex ].label = label;
		updateTabs( tripTabs );
	};
	return (
		<div className="wp-travel-trip-edit-block-tab-list">
			<ul>
				{ tripTabs.map( ( tab, index ) => {
					if ( 'yes' === tab.show_in_menu ) {
						return <li key={ `tab-${ tab.tab_key }` } className={ tab.tab_key === props.selectedTab ? 'active-tab' : '' }>
							<Button onClick={ () => {
								swithTab( tab );
							} }>
								<RichText
									tagName="p" // The tag here is the element output and editable in the admin
									value={ tab.label } // Any existing content, either from the database or an attribute default
									allowedFormats={ [] } // Allow the content to be made bold or italic, but do not allow other formatting options
									onChange={ ( content ) => {
										updateLabel( content, index );
									} } // Store updated content as a block attribute
									placeholder={ tab.default_label } // Display this text before any content has been added by the user
								/>
							</Button>

						</li>;
					}
				} ) }
			</ul>
		</div>
	);
};
const PreviewMode = ( ) => {
	return <p className="description">{ __( 'This is preview mode.' ) }</p>;
};

const TabsDisplayView = ( props ) => {
	const { tabs, swithTab, allData, selectedTab } = props;
	const tripTabs = ! isEmpty( tabs ) ? tabs : [];
	return <>
		<div className="wp-travel-trip-edit-block-tab-list">
			<ul>
				{ tripTabs.map( ( tab ) => {
					return <li key={ `tab-${ tab.tab_key }` } className={ tab.tab_key === props.selectedTab ? 'active-tab' : '' }>
						<Button onClick={ () => {
							swithTab( tab );
						} }>
							{ tab.label }
						</Button>

					</li>;
				} ) }
			</ul>
		</div>
		<div className="wptravel-tab-content-wrapper">
			{ tripTabs.map( ( tab, index ) => {
				const tab_key = tab.tab_key ? tab.tab_key.replaceAll( ' ', '_' ).toLowerCase() : '';
				return <Fragment key={ `tab-${ tab.tab_key }-${ index }` }>{ <div style={ {
					display: tab.tab_key === selectedTab ? 'block' : 'none',
				} }>
					{ tab_key && applyFilters( `wptravel_trip_edit_block_tab_view_${ tab_key }`, [], allData ) }
				</div> }
				</Fragment>;
			} ) }
		</div>
	</>;
};
class edit extends Component {
	constructor( props ) {
		super( props );
		this.swithTab = this.swithTab.bind( this );
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			selectedTab: 'overview',
			selectedTabSet: false,
			isBookingModelOpen: false,
			isEditing: true,
		};
	}
	componentDidUpdate() {
		const { firstTab } = this.props;

		if ( ! this.state.selectedTabSet ) {
			this.setState( {
				selectedTab: firstTab,
				selectedTabSet: true,
			} );
		}
	}

	swithTab( tab ) {
		this.setState( {
			selectedTab: tab.tab_key,
		} );
	}

	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}

	render() {
		const { tabs, allData, isSelected } = this.props;
		const tripTabs = ! isEmpty( tabs ) ? tabs : [];
		const blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';
		return <div className={ `wptravel-block-wrapper wptravel-block-tabs ${ blockClass }` }>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			{ this.state.isEditing && <><TabHeadings { ...this.props } swithTab={ this.swithTab } selectedTab={ this.state.selectedTab } />
				<div className="wptravel-tab-content-wrapper">
					{ tripTabs.map( ( tab, index ) => {
						const tab_key = tab.tab_key ? tab.tab_key.replaceAll( ' ', '_' ).toLowerCase() : '';

						return <Fragment key={ `tab-${ tab.tab_key }-${ index }` }>{ <div style={ {
							display: tab.tab_key === this.state.selectedTab ? 'block' : 'none',
						} }>
							{ tab_key && applyFilters( `wptravel_trip_edit_block_tab_${ tab_key }`, [], allData ) }
							{ tab_key && addFilter( `wptravel_trip_edit_block_tab_view_${ tab_key }`, `WPTravel/TripEdit/Preview${ tab_key }`, PreviewMode, 10 ) }

							{/* Global Custom Tabs */}
							{ has( tab, 'custom' ) && 'yes' === tab.custom &&
								<span
									dangerouslySetInnerHTML={ { __html: tab.content } }
								></span> }
						</div> }
						</Fragment>;
					} ) }
			</div></> }
			{ ! this.state.isEditing && <TabsDisplayView { ...this.props } selectedTab={ this.state.selectedTab } swithTab={ this.swithTab } /> }
			{/* <TabsDisplayView { ...this.props } selectedTab={ this.state.selectedTab } swithTab={ this.swithTab } />
			{ tripTabs.map( ( tab, index ) => {
				const tab_key = tab.tab_key ? tab.tab_key.replaceAll( ' ', '_' ).toLowerCase() : '';

				return <Fragment key={ `tab-${ tab.tab_key }-${ index }` }>{ <div style={ {
					display: tab.tab_key === this.state.selectedTab ? 'block' : 'none',
				} }>
					{ tab_key && addFilter( `wptravel_trip_edit_block_tab_view_${ tab_key }`, `WPTravel/TripEdit/Preview${ tab_key }`, PreviewMode, 10 ) }
				</div> }
				</Fragment>;
			} ) } */}
		</div>;
	}
}

export default compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData, getSettings } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData(tripId);
	let tabs       = has( tripData, 'trip_tabs' ) ? tripData.trip_tabs : [];
	let firstTab = 'overview';

	// if ( has( tripData, 'trip_tabs' ) ) {
	// 	firstTab = first( tripData.trip_tabs ).tab_key;
	// }

	// Global tab
	const useGlobal = tripData.use_global_tabs;
	if ( 'yes' === useGlobal ) {
		const settings = getSettings();
		tabs           = has( settings, 'global_tab_settings' ) ? settings.global_tab_settings : tabs;
	}
	if ( tabs.length > 0 ) {
		firstTab = first( tabs ).tab_key;
	}

	return {
		allData: tripData,
		globalTabs: has( tripData, 'settings.global_tab_settings' ) ? tripData.settings.global_tab_settings : [],
		tabs,
		settings: tripData.settings,
		firstTab,
	};
} ), withDispatch( ( dispatch ) => {
	const { updateTripData } = dispatch( 'WPTravel/TripEdit' );
	return {
		updateTabs( tabs ) {
			const _tripData = {
				trip_tabs: [ ...tabs ],
			};
			updateTripData( {
				..._tripData,
			} );

			const { editPost } = dispatch( 'core/editor' );
			editPost( {
				meta: {
					wp_travel_tabs: JSON.stringify( tabs ),
				},
			} );
		},
	};
} ) )( edit );
