import { Fragment } from '@wordpress/element';
import { Notice } from '@wordpress/components';
import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';

const Reviews = ( ) => {
	return <Notice status="notice" isDismissible={ false }>{ __( 'Reviews are dynamic content. So can be viewed in frontend.' ) }</Notice>;
};

addFilter( 'wptravel_trip_edit_block_tab_reviews', 'WPTravel/TripEdit/Block/Tab/Reviews/ReviewsNotice', ( content ) => {
	return [ ...content, <Fragment key="wp_travel_block_trip_edit_tab_trip_reviews">
		<h3 className="wp-travel-tab-content-title">{ __( 'Reviews' ) }</h3>
		<Reviews />
	</Fragment> ];
}, 9 );
