/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { PluginSidebar, PluginSidebarMoreMenuItem } from '@wordpress/edit-post';
import { registerPlugin } from '@wordpress/plugins';
// import { cog as icon } from '@wordpress/icons';
import { PanelBody, Button, Spinner } from '@wordpress/components';
import { useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Inner dependencies.
 */
import { WPTravelLogo as icon } from '../../components/Icons';
import TemplatesModel from '../../components/TemplatesModel';

const Component = () => {
	const [ { isTemplatesOpen, resetting }, setState ] = useState( {
		isTemplatesOpen: false,
		resetting:false
	} );
	const openModal = () =>
		setState( {
			isTemplatesOpen: true,
		} );
	const closeModal = () =>
		setState( {
			isTemplatesOpen: false,
		} );
	return (
		<>
			<PluginSidebarMoreMenuItem target="wptravel-sidebar">
				{ __( 'WP Travel Options' ) }
			</PluginSidebarMoreMenuItem>
			<PluginSidebar
				name="wptravel-sidebar"
				title={ __( 'WP Travel Options' ) }
				icon={ icon }
			>
				<PanelBody>
					<Button onClick={ () => openModal() } icon="admin-appearance" isSecondary>{ __( 'Browse Templates' ) }</Button>
					{ isTemplatesOpen && <TemplatesModel
						onRequestClose={ () => closeModal() }
					/> }
				</PanelBody>
				<PanelBody>
					<Button onClick={ () => {
						if (confirm('This will reset the template. Do you want to continue?')) {
							setState( {
								resetting: true,
							} );

							apiFetch({ url: `${ajaxurl}?action=wptravel_reset_template&_nonce=${_wp_travel_admin._nonce}`, data: { reset_template: true, trip_id:_wp_travel.postID }, method: 'post' }).then(res => {

								setState( {
									resetting: false,
								} );
								window.location.reload()
							});
						}
					} } icon="update" isSecondary>{ __( 'Reset Template' ) }</Button>
					{ resetting && <Spinner /> }
				</PanelBody>
			</PluginSidebar>
		</>
	);
};

registerPlugin( 'wptravel-sidebar', {
	icon,
	render: Component,
} );
