import { Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';
import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { RichText } from '@wordpress/block-editor';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

import { has } from 'lodash';

const Excludes = compose( withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );

	return {
		exclude: has( tripData, 'trip_exclude' ) ? tripData.trip_exclude : '',
	};
} ),
withDispatch( ( dispatch ) => {
	const { updateTripData } = dispatch( 'WPTravel/TripEdit' );
	return {
		updateExcludes( excludes ) {
			updateTripData( {
				trip_exclude: excludes,
			} );
			const { editPost } = dispatch( 'core/editor' );
			editPost( {
				meta: {
					wp_travel_trip_exclude: excludes,
				},
			} );
		},
	};
} ) )( ( { exclude, updateExcludes } ) => {
	return <RichText
		tagName="p" // The tag here is the element output and editable in the admin
		value={ exclude } // Any existing content, either from the database or an attribute default
		// allowedFormats={ [] } // Allow the content to be made bold or italic, but do not allow other formatting options
		onChange={ ( content ) => {
			updateExcludes( content );
		} } // Store updated content as a block attribute
		placeholder={ __( 'Trip Excludes' ) } // Display this text before any content has been added by the user
		className="wp-travel-block-rich-text"
	/>;
} );

addFilter( 'wptravel_trip_edit_block_tab_trip_excludes', 'WPTravel/TripEdit/Block/Tab/TripExcludes/Excludes', ( content ) => {
	return [ ...content, <Fragment key="wp_travel_block_trip_edit_tab_trip_excludes">
		{/* <h3 className="wp-travel-tab-content-title">{ __( 'Trip Excludes' ) }</h3> */}
		<Excludes />
	</Fragment> ];
}, 9 );
