/**
 * External dependencis.
 */
import { Gmaps, Marker } from 'react-gmaps';
import Geocode from 'react-geocode';

/**
 * WordPress dependencies.
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import {
	select,
	withSelect,
	withDispatch,
} from '@wordpress/data';
import { addFilter, applyFilters } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { isEmpty, has } from 'lodash';
import { Placeholder, PanelBody, ToggleControl, TextControl, Notice, Button, BaseControl } from '@wordpress/components';
const { getCurrentPostId } = wp.data.select( 'core/editor' );

/**
 * Inner dependencies.
 */
import BaseWrapper from '../../components/BaseWrapper';
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );
		this.updateMapFromMarker = this.updateMapFromMarker.bind( this );
		// bind function
		this.setIsEditing = this.setIsEditing.bind( this );
		
		// this.MapControls  = this.MapControls.bind( this );
		this.state = {
			isEditing: false,
		};
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}
	updateMapFromMarker( e ) {
		const { changeLocation, settings } = this.props;
		const data = {};
		data.lat = String( e.latLng.lat() );
		data.lng = String( e.latLng.lng() );
		data.loc = '';
		data.use_lat_lng = 'no';

		Geocode.setApiKey( settings.google_map_api_key );
		Geocode.fromLatLng( e.latLng.lat(), e.latLng.lng() ).then( ( response ) => {
			// quick fix.
			data.loc = response.results[ 0 ].formatted_address;
			changeLocation( data );
		}, ( ) => {
			// console.error( error );
		} );

		changeLocation( data );
	}

	render() {
		const { className, isSelected, location, settings } = this.props;
		const address = has( location, 'loc' ) && ! isEmpty( location.loc ) && location.loc;
		const lat = has( location, 'lat' ) && ! isEmpty( location.lat ) && location.lat;
		const lng = has( location, 'lng' ) && ! isEmpty( location.lng ) && location.lng;
		const language = 'en';
		const zoom = 12;
		const height = 200;
		const hasApiKey = has( settings, 'google_map_api_key' ) && ! isEmpty( settings.google_map_api_key );

		let mapAddress = address ? address : 'kathmandu';
		let blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';
		return <div className={ `wptravel-block-wrapper  wptravel-block-map ${blockClass} ${className}` }>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			
			{ ! this.state.isEditing && applyFilters('wptravel_trip_edit_block_map_area_view', [], settings, location, this.props)}
			
		</div>;
	}
}

// Create a higher-order component that updates automatically when featured image changes.
const applyWithSelect = withSelect( ( select ) => {
	const tripId = getCurrentPostId();
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );
	return {
		location: tripData.map_data,
		settings: tripData.settings,
	};
} );

const applyWithDispatch = withDispatch( ( dispatch, ownProps ) => {
	const tripId = getCurrentPostId();
	const { updateTripData } = dispatch( 'WPTravel/TripEdit' );
	const { getTripData } = select( 'WPTravel/TripEdit' );
	const tripData = getTripData( tripId );
	return {
		changeLocation( location ) {
			const _tripData = {
				map_data: {
					...tripData.map_data,
					...location,
				},
			};
			updateTripData( {
				..._tripData,
			} );
			const { editPost } = dispatch( 'core/editor' );
			editPost( {
				meta: {
					wp_travel_lat: String( _tripData.map_data.lat ),
					wp_travel_lng: String( _tripData.map_data.lng ),
					wp_travel_trip_map_use_lat_lng: _tripData.map_data.use_lat_lng,
					wp_travel_location: _tripData.map_data.loc,
				},
			} );
		},
	};
} );

export default compose( [
	applyWithSelect,
	applyWithDispatch,
] )( edit );

