/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
const { __ } = wp.i18n; // Import __() from wp.i18n
const { InspectorControls } = wp.editor; // Import
const { PanelBody, TextControl, Disabled, ToggleControl } = wp.components; // Import Setting Controls
import ServerSideRender from '@wordpress/server-side-render';

const $ = window.jQuery;

/**
 * External dependencies.
 */
import { isEmpty, has } from 'lodash';

/**
 * Inner dependencies.
 */
import Inspector from './inspector';

class edit extends Component {
	constructor( props ) {
		super( props );

		// bind function
		this.formSubmitHandler = this.formSubmitHandler.bind( this );
		this.setIsEditing = this.setIsEditing.bind( this );

		this.state = {
			isEditing: false,
		};
	}

	// Prevent Backend Form Submission
	formSubmitHandler( e ) {
		e.preventDefault();
		return false;
	}
	setIsEditing( status ) {
		this.setState( {
			isEditing: status,
		} );
	}

	// componentDidUpdate() {
	// 	// Filter Range Slider Widget.
	// 	$( '.wp-travel-range-slider' ).slider( {
	// 		range: true,
	// 		min: 50,
	// 		max: 1000,
	// 		values: [ 50, 250 ],
	// 		slide( event, ui ) {
	// 			$( '.price-amount' ).val( '$' + ui.values[ 0 ] + ' - ' + '$' + ui.values[ 1 ] );
	// 		},
	// 	} );
	// }
	render() {
		const { attributes, setAttributes, isSelected } = this.props;
		const {
			title,
			heading,
			keyWordSearch,
			tripFact,
			tripTypeFilter,
			tripLocationFilter,
			priceOrderFilter,
			priceRangeFilter,
			tripDateFilter,
		} = attributes;
		const blockClass = this.state.isEditing ? 'wptravel-block-edit' : 'wptravel-block-preview';

		return <div className={ `wptravel-block-wrapper wptravel-block-trip-filters ${ blockClass }` }>
			{ isSelected && <Inspector { ...this.props } setIsEditing={ this.setIsEditing } isEditing={ this.state.isEditing } /> }
			{ this.state.isEditing &&
				<>
					<strong>{ __( 'Trip Filter Options' ) }</strong>
					<div className="wptravel-edit-options" >
						
					</div>
				</>
			}

			{ ! this.state.isEditing && <ServerSideRender
				block="wptravel/trip-filters"
				attributes={ this.props.attributes }
			/> }

		</div>;
	}
}
export default edit;
