/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl } from '@wordpress/block-editor';
import { ToolbarGroup, PanelBody, TextControl, SelectControl, ToggleControl } from '@wordpress/components';
import { applyFilters } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";


/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { setIsEditing, isEditing, setAttributes, attributes, isSelected, openBlockSetting } = this.props;
		const { textAlign, displayInline, showTooltip, tooltipText } = attributes;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				{ isSelected &&
					<BlockControls>
						<ToolbarGroup controls={ toolbarControls } />
						{/* <AlignmentControl
							value={textAlign}
							onChange={ ( nextAlign ) => {
								setAttributes( { textAlign: nextAlign } );
							} }
						/> */}
					</BlockControls>
				}
				<InspectorControls>
					<PanelBody title={ __( 'Trip Calendar Options' ) } >
						<ToggleControl
							label={ __( 'Show Calendar' ) }
							help={ __( 'This will display calendar directly instead of input field.' ) }
							checked={ displayInline }
							onChange={ () => setAttributes( { displayInline: ! displayInline } ) }
						/>
						{ /* <ToggleControl
							label={ __( 'Show Tooltip' ) }
							help={ __( 'This will display tooltip besides calendar' ) }
							checked={ showTooltip }
							onChange={ () => setAttributes( { showTooltip: ! showTooltip } ) }
						/> */ }
						{ showTooltip && <TextControl
							label={ __( 'Tooltip text' ) }
							value={ tooltipText }
							type="text"
							onChange={ ( value ) => setAttributes( { tooltipText: value } ) }
						/> }
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	}
}

const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);
