/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { PanelBody, ToolbarGroup } from '@wordpress/components';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { edit } from '@wordpress/icons';
import { compose } from "@wordpress/compose";
import { withDispatch } from "@wordpress/data";

/**
 * Inner dependencies.
 */

/**
 * Inspector controls
 */
class Inspector extends Component {
	render() {
		const { allData, setIsEditing, isEditing, openBlockSetting } = this.props;
		const toolbarControls = [
			{
				icon: edit,
				title: __( 'Edit tabs' ),
				onClick: () => openBlockSetting(),
				isPressed: isEditing,
			},
		];
		return (
			<Fragment>
				<BlockControls>
					<ToolbarGroup controls={ toolbarControls } />
				</BlockControls>
				<InspectorControls>
					{ applyFilters( 'wptravel_trip_edit_block_tab_inspector_controls', [], allData ) }
				</InspectorControls>
			</Fragment>
		);
	}
}

const applyWithDispatch = withDispatch((dispatch, ownProps) => {
	return {
		openBlockSetting: () => {
			dispatch( 'core/block-editor' ).selectBlock( ownProps.clientId );
			dispatch( 'core/edit-post' ).openGeneralSidebar( 'edit-post/block' )
		}
	};
});
export default compose([applyWithDispatch])(Inspector);

addFilter( 'wptravel_trip_edit_block_tab_inspector_controls', 'WPTravel/TripEdit/Block/Tab/InspectorControls/TabSorting', ( content, allData ) => {
	return [ ...content, <PanelBody title={ __( 'Tabs' ) } initialOpen={ true } key="wptravel_trip_edit_block_tab_inspector_controls_tab_sorting">
		{ applyFilters( 'wptravel_trip_edit_block_tab_inspector_controls_tab_sort', [], allData ) }
	</PanelBody> ];
} );
